@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Playfair+Display+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#catalogHeader {
    font-family: zapfino;
    font-size: 400%;
  }
  
  #catalogTagline {
    font-family: 'Montserrat', sans-serif;
    font-size: 150%;
  }
  
  #signupEmailForm {
    width: 25%;
  }
#ContactFormPage {
    padding-bottom: 8%;
}

.divider {
    padding-top: 1%;
}

#contactFormEmailInput {
    width: 45%;
}

#contactFormText {
    width: 45%;
}
#submissionButton {
    align-content: center;
}
#LoginFormPage {
    padding-bottom: 12%;
}

#divider {
    padding-bottom: 1%;
}
#pricingCalculatorElements {
    align-content: center;
